.menu-items:focus-visible {
  border: 1px solid #262638;
}
.address-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: white !important;
}

.user-address {
  margin-left: 1rem;
  margin-right: 1rem;
}

.App-header-user-address:hover {
  background: #808aff14;
}
.App-header-user-address:active {
  background: var(--dark-blue-active);
}

.menu-items {
  position: absolute;
  right: 0;
  top: 4.3rem;
  min-width: 15.5rem;
  width: 100%;
  transform-origin: top right;
  border-radius: 1.2rem;
  padding: 0.8rem 0;
  background: rgba(49, 49, 49);

  list-style: none;
  cursor: pointer;
  outline: none;
  z-index: 1000;
}
.menu-item {
  display: flex !important;
  align-items: center;
  font-size: var(--font-base);
  color: #a0a3c4;
  padding-bottom: 1.5rem;
  font-size: var(--font-sm);
  padding: 0.85rem 0.8rem;
  border-radius: 0.4rem;
}
.menu-item:hover {
  background: rgba(39, 39, 39, 0.6) !important;
  border-radius: 0.4rem;
  opacity: 1;
  color: #eee;
  transition: 0.3s all;
}
.menu-item > p {
  margin: 0px;
  padding-left: 1rem;
}
.menu-item > a {
  display: inline-flex;
}

@media screen and (max-width: 370px) {
  .user-address {
    margin-left: 0;
  }
  .address-btn {
    display: flex;
    justify-content: space-between;
  }
  .user-avatar {
    display: none;
  }
}
